import React, { useState } from "react";
import './NewExpense.css';
import ExpenseForm from './ExpenseForm';

const NewExpense = (props) => {
  const [formEnable, setFormEnable] = useState(false);
  const saveExpenseDataHandler = (enteredExpenseData) => {
    const expenseData = {
      ...enteredExpenseData,
      id: Math.random().toString()    };
    props.onAddExpense(expenseData);
  }
  const buttonStateHandler = (buttonState) => {
    setFormEnable(buttonState);
  }
  const buttonHandler = (event) => {
    event.preventDefault();

    setFormEnable(true);

  }
  return (
    <div className="new-expense">
      {formEnable ? <ExpenseForm onButtonState={buttonStateHandler} onSaveExpenseData={saveExpenseDataHandler}/>
:       <button onClick={buttonHandler}>Add New Expense</button>
}
    </div>
  );
};

export default NewExpense;
